html {
  height: 100%;
}
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.toggle-button {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  width: 30px;
  height: 24px;
  background: transparent;
  border: none;
  cursor: pointer;
  padding: 0;
  box-sizing: border-box;
}
.toggle-button:focus {
  outline: none;
}
.toggle-button__line {
  width: 30px;
  height: 1px;
  background: white;
}
@media (min-width: 700px) {
  .toggle-button__line {
    background: black;
  }
}
.toolbar {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1;
  height: 56px;
}
.toolbar__navigation {
  width: 80%;
  margin: auto;
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 1rem;
}
.toolbar__logo {
  margin-left: 1rem;
}
.toolbar__logo a {
  color: white;
  text-decoration: none;
  font-size: 1.5rem;
}

.footer {
  width: 100%;
  background: grey;
}
@media (min-width: 700px) {
  .footer {
    padding: 3em 10%;
    position: absolute;
    bottom: 0;
    left: 0;
    background: none;
    color: grey;
  }
}

.side-drawer {
  height: 100%;
  background: pink;
  position: fixed;
  right: 0;
  bottom: 0;
  width: 70%;
  max-width: 400px;
  z-index: 2;
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
  transition: -webkit-transform .5s ease-out;
  transition: transform .5s ease-out;
  transition: transform .5s ease-out, -webkit-transform .5s ease-out;
}
.side-drawer.open {
  -webkit-transform: translateX(0);
          transform: translateX(0)
}
.side-drawer ul {
  height: 100%;
  list-style: none;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.side-drawer li {
  margin: 0.5rem 0;
}
.side-drawer a{
  color: black;
  text-decoration: none;
  font-size: 1.2rem;
}
.side-drawer a:hover,
.side-drawer a:active {
  color: #fa923f;
}

.backdrop {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0 ,0.3);
  z-index: 1;
}
.picture-section-wrap {
  height: 70vh;
}
@media (min-width: 700px) {
  .picture-section-wrap {
    width: 65%;
    height: 100vh;
  }
}

.description-section-wrap {
  background: pink;
}
@media (min-width: 700px) {
  .description-section-wrap {
    flex: 1 1;
  }
}

@media (min-width: 700px) {
  .home-wrap {
    display: flex;
    align-items: center;
  }
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
img {
  width: 100%;
  height: 100%;
  display: block;
  object-fit: cover;
}
.App {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  position: relative;
}
main {
  flex: 1 1;
}

